import React from 'react';
import { Table, Content, Media, Modal, Button } from 'react-bulma-components';
import { rounds } from "../data/bracket-team-data";
import Chart from "./Chart";
import MatchDetailRow from "./MatchDetailRow"
import axios from 'axios';


class MatchDetails extends React.Component {


    fetchData = (ticker, startdate, enddate) => {
      const url = `./data/historical/` + ticker +  `.json`;

      return axios.get(url).then((response) => {

        let historicaldata = response.data.filter( ({date}) => date >= startdate && date <= enddate);
        // console.log(historicaldata);

        // const result = d3.range(historicaldata.length).map((item, i) => {
        //   const open = historicaldata[i].open;
        //   const close = historicaldata[i].close;
        //   const high = historicaldata[i].high;
        //   const low = historicaldata[i].low;
        //   const volume = historicaldata[i].volume;

        //   return {
        //     date: historicaldata[i].date,
        //     open,
        //     high,
        //     low,
        //     close,
        //     volume
        //   };
        // });
        this.setState({ chartData: historicaldata, chartTicker: ticker });
      });


    };

    changeData = (ticker) => {
      this.fetchData(ticker, rounds[this.props.round].start, rounds[this.props.round].end);
    };

    state = {
      chartData: [],
      chartTicker: "STOCK"

      // teamAData: this.fetchData('CVS', '2021-04-01', '2021-04-30'),
      // teamBData: this.fetchData('WBA')
    };
    // const [data, setData] = useState(generateData());

  render() {
    const chart_width = 500;
    const chart_height = 300;

    let finalPriceLabel = "Current";

    if (this.props.teamA !== undefined && this.props.teamB !== undefined) {
      let teamAFinalPrice = this.props.teamA['prices'].find(p => {
        return p.date === rounds[this.props.round].end
      });

      finalPriceLabel = teamAFinalPrice === undefined ? "Current": "End";
    }


    return (

        <Modal
          show = {this.props.show}
          onClose = {this.props.onClose}
        >
          <Modal.Card>
            <Modal.Card.Header showClose>
              <Modal.Card.Title>{this.props.teamA.name} vs {this.props.teamB.name} between {rounds[this.props.round].start} to {rounds[this.props.round].end}</Modal.Card.Title>
            </Modal.Card.Header>
            <Modal.Card.Body>
              <Media>
                {/* <Media.Item renderAs="figure" align="left">
                  <Image
                    size={64}
                    alt="64x64"
                    src="http://bulma.io/images/placeholders/128x128.png"
                  />
                </Media.Item> */}
                <Media.Item>
                  <Content>
                    <Table>
                      <thead>
                        <tr>
                            <th>Ticker</th>
                            <th>Start</th>
                            <th>{finalPriceLabel}</th>
                            <th>Mkt Cap</th>
                            {/* <th>Ent Val</th> */}
                            <th>52 wk range</th>
                            {/* <th>p/e</th>
                            <th>div</th>
                            <th>beta</th>
                            <th>IV</th> */}
                            <th>FV</th>
                            <th>Y!</th>

                        </tr>
                      </thead>
                      <tbody>
                        <MatchDetailRow
                        team={this.props.teamA}
                        round={this.props.round}
                        >
                          </MatchDetailRow>
                        <MatchDetailRow
                        team={this.props.teamB}
                        round={this.props.round}
                        >
                          </MatchDetailRow>
                      </tbody>
                    </Table>

                    <div className="chartContent">
                      <div>
                      </div>
                      <div>
                        <Chart data={this.state.chartData} width={chart_width} height={chart_height} />
                      </div>
                      <span>{this.state.chartTicker}</span>
                    </div>


                  </Content>
                </Media.Item>
              </Media>
            </Modal.Card.Body>
            <Modal.Card.Footer renderAs={Button.Group} align="center" hasAddons>
              <Button onClick={() => this.changeData(this.props.teamA.name)}>{this.props.teamA.name}</Button>
              <Button onClick={() => this.changeData(this.props.teamB.name)}>{this.props.teamB.name}</Button>
            </Modal.Card.Footer>
          </Modal.Card>
        </Modal>
    );
  };

}
  MatchDetails.argTypes = {
    showClose: {
      control: {
        type: 'boolean',
      },
    },
  };

export default MatchDetails;