import React from "react";
import { Box, Table } from 'react-bulma-components';
// import Box from 'react-bulma-components/lib/components/box';
// import Table from 'react-bulma-components/lib/components/table';
import IndexRow from './IndexRow';

class Indexes extends React.Component {
  state = {
  };


  calculateCumulative = (ticker) => {

    console.log('Calculating total return for ' + ticker);


    let costBasis = 0;
    let currentValue = 0;

    for (const j in this.props.rounds) {

        let score = 0;

        if (this.props.teams[ticker].scores[j] !== undefined) {
            score = this.props.teams[ticker].scores[j].score
        } else if (this.props.rounds[j].start < "2021-06-11") {
            score = this.props.teams[ticker].currentRoundScore
        } else {
          break;
        }

        costBasis += this.props.rounds[j].incremental;
        currentValue = (1 + score) * (this.props.rounds[j].incremental + currentValue);

        console.log('round: ' + j + ' costBasis: ' + costBasis + ' score: ' + score + ' currentValue: ' + currentValue);
      }


    let cummulativeScore = currentValue / costBasis - 1;


    this.props.teams[ticker]["cumulativeTotal"] = cummulativeScore;

  }


  render() {
    this.calculateCumulative("SPY");
    this.calculateCumulative("QQQ");
    this.calculateCumulative("RSP");
    this.calculateCumulative("DIA");
    this.calculateCumulative("STONKS");

    return (
      <Box className={'has-background-light'}>
          <Table>
              <thead>
                  <tr>
                      <th>Ticker</th>
                      <th>{this.props.rounds[0].name}</th>
                      <th>{this.props.rounds[1].name}</th>
                      <th>{this.props.rounds[2].name}</th>
                      <th>{this.props.rounds[3].name}</th>
                      <th>{this.props.rounds[4].name}</th>
                      <th>{this.props.rounds[5].name}</th>
                      <th>Cumulative</th>
                  </tr>
              </thead>
              <tbody>
                  <IndexRow
                    ticker="SPY"
                    rounds={this.props.rounds}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    >
                  </IndexRow>
                  <IndexRow
                    ticker="QQQ"
                    rounds={this.props.rounds}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    >
                  </IndexRow>
                  <IndexRow
                    ticker="RSP"
                    rounds={this.props.rounds}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    >
                  </IndexRow>
                  <IndexRow
                    ticker="DIA"
                    rounds={this.props.rounds}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    >
                  </IndexRow>
                  <IndexRow
                    ticker="STONKS"
                    rounds={this.props.rounds}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    >
                  </IndexRow>
              </tbody>
          </Table>
      </Box>
    );
  }
}

export default Indexes;