import React from 'react';
import { rounds } from "../data/bracket-team-data";


class MatchDetailRow extends React.Component {



  render() {
    let teamStartPrice = 0;
    let teamfinalPriceValue = 0;
    let teammarketCap = 0;

    if (this.props.team !== undefined) {

      teamStartPrice = this.props.team['prices'].find(p => {
        return p.date === rounds[this.props.round].start
      });

      let teamFinalPrice = this.props.team['prices'].find(p => {
        return p.date === rounds[this.props.round].end
      });

      let teamCurrentPrice = this.props.team.latestPrice;


      teamfinalPriceValue = teamFinalPrice === undefined ? teamCurrentPrice: teamFinalPrice.close;
      teammarketCap = Math.round((teamfinalPriceValue * this.props.team.sharesOutstanding / 10000000)) / 100  ;

    }

    return (
        <tr>
            <td>{this.props.team.name}</td>
            <td>${Math.round(teamStartPrice.open * 100) / 100}</td>
            <td>${Math.round(teamfinalPriceValue * 100) / 100}</td>
            <td>${teammarketCap}B</td>
            {/* <td>EV</td> */}
            <td>${Math.round(this.props.team.low52wk * 100) / 100}-${Math.round(this.props.team.high52wk * 100) / 100}</td>
            {/* <td>PE</td>
            <td>div</td>
            <td>beta</td>
            <td>iv</td> */}
            <td><a href={"https://finviz.com/quote.ashx?t=" + this.props.team.name} target='_blank' rel='noreferrer'>FV</a></td>
            <td><a href={"https://finance.yahoo.com/quote/" + this.props.team.name} target='_blank' rel='noreferrer'>Y!</a></td>
        </tr>
    );
  };

}

export default MatchDetailRow;