import React from "react";
import Indexes from "./Indexes";
import { Columns, Heading, Progress } from 'react-bulma-components';
// import Columns from 'react-bulma-components/lib/components/columns';
// import Heading from 'react-bulma-components/lib/components/heading';
// import Progress from 'react-bulma-components/lib/components/progress';

class Header extends React.Component {

    render() {



        return (
            <div>
                <Columns>

                    <Columns.Column size={this.props.titleWidth}>
                        <Heading className={"title is-1 is-spaced"}>Stonk Madness</Heading>
                        <Heading className={"subtitle"}>Score as of {this.props.scoreDate}</Heading>
                    </Columns.Column>

                    <Columns.Column size={this.props.compsWidth}>
                        <Indexes
                            rounds={this.props.rounds}
                            teams={this.props.teams}
                            bracket={this.props.bracket}
                        >
                        </Indexes>
                    </Columns.Column>

                </Columns>
                <Columns>

                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[0].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[1].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[2].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[3].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[4].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[5].show &&
                        <Columns.Column size={this.props.championshipWidth}><Heading className={"title is-4"}>{this.props.rounds[5].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[4].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[3].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[2].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[1].name}</Heading></Columns.Column>
                    }
                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Heading className={"title is-4"}>{this.props.rounds[0].name}</Heading></Columns.Column>
                    }
                </Columns>
                <Columns>

                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[0].start} to {this.props.rounds[0].end}</Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[1].start} to {this.props.rounds[1].end}</Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[2].start} to {this.props.rounds[2].end}</Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[3].start} to {this.props.rounds[3].end}</Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[4].start} to {this.props.rounds[4].end}</Columns.Column>
                    }
                    {this.props.rounds[5].show &&
                        <Columns.Column size={this.props.championshipWidth}>{this.props.rounds[5].start} to {this.props.rounds[5].end}</Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[4].start} to {this.props.rounds[4].end}</Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[3].start} to {this.props.rounds[3].end}</Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[2].start} to {this.props.rounds[2].end}</Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[1].start} to {this.props.rounds[1].end}</Columns.Column>
                    }
                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}>{this.props.rounds[0].start} to {this.props.rounds[0].end}</Columns.Column>
                    }
                </Columns>
                <Columns>

                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[0].tradingDaysElapsed} max={this.props.rounds[0].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[1].tradingDaysElapsed} max={this.props.rounds[1].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[2].tradingDaysElapsed} max={this.props.rounds[2].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[3].tradingDaysElapsed} max={this.props.rounds[3].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[4].tradingDaysElapsed} max={this.props.rounds[4].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[5].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[5].tradingDaysElapsed} max={this.props.rounds[5].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[4].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[4].tradingDaysElapsed} max={this.props.rounds[4].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[3].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[3].tradingDaysElapsed} max={this.props.rounds[3].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[2].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[2].tradingDaysElapsed} max={this.props.rounds[2].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[1].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[1].tradingDaysElapsed} max={this.props.rounds[1].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                    {this.props.rounds[0].show &&
                        <Columns.Column size={this.props.earlyRoundWidth}><Progress className={'is-info'} value={this.props.rounds[0].tradingDaysElapsed} max={this.props.rounds[0].tradingDaysTotal}>90%</Progress></Columns.Column>
                    }
                </Columns>
            </div>
        );
    }
}


export default Header;
