import React from "react";
import { Box } from 'react-bulma-components';
// import Icon from 'react-bulma-components/lib/components/icon';
// import Box from 'react-bulma-components/lib/components/box';


class WinnerArrow extends React.Component {





  render() {

    const leftDivisions = ['a', 'b']
    const rightDivisions = ['c', 'd']

    return (
      <Box className={'has-background-light'}>

        {leftDivisions.includes(this.props.division) && this.props.matchId % 2 === 0 &&
            <Box>
                <span class="icon ">
                    <i class="fa fa-level-down-alt is-size-1"></i>
                </span>
            </Box>
        }
        {leftDivisions.includes(this.props.division) && this.props.matchId % 2 === 1 &&
            <Box>
                <span class="icon">
                    <i class="fa fa-level-up-alt is-size-1"></i>
                </span>
            </Box>
        }



        {rightDivisions.includes(this.props.division) && this.props.matchId % 2 === 0 &&
            <Box>
                <span class="icon">
                    <i class="fa fa-level-up-alt fa-flip-horizontal fa-flip-vertical is-size-1"></i>
                </span>
            </Box>
        }
        {rightDivisions.includes(this.props.division) && this.props.matchId % 2 === 1 &&
            <Box>
                <span class="icon ">
                    <i class="fa fa-level-down-alt fa-flip-horizontal fa-flip-vertical is-size-1"></i>
                </span>
            </Box>
        }
      </Box>
    );
  }
}

export default WinnerArrow;