import React from "react";

class IndexRow extends React.Component {
  state = {
  };



  render() {
    return (
        <tr>
            <td>{this.props.teams[this.props.ticker].name}</td>
            <td>{this.props.teams[this.props.ticker].scores[0] ? Math.round(this.props.teams[this.props.ticker].scores[0].score * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].scores[1] ? Math.round(this.props.teams[this.props.ticker].scores[1].score * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].currentRoundScore ? Math.round(this.props.teams[this.props.ticker].currentRoundScore * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].scores[3] ? Math.round(this.props.teams[this.props.ticker].scores[3].score * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].scores[4] ? Math.round(this.props.teams[this.props.ticker].scores[4].score * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].scores[5] ? Math.round(this.props.teams[this.props.ticker].scores[5].score * 10000) / 100 : 0}</td>
            <td>{this.props.teams[this.props.ticker].cumulativeTotal ? Math.round(this.props.teams[this.props.ticker].cumulativeTotal * 10000) / 100 : 0}</td>
        </tr>
    );
  }
}

export default IndexRow;