class Team extends React.Component {
  // static propTypes = {
  //   details: PropTypes.shape({
  //     image: PropTypes.string,
  //     name: PropTypes.string,
  //     desc: PropTypes.string,
  //     status: PropTypes.string,
  //     price: PropTypes.number
  //   }),
  //   addToOrder: PropTypes.func
  // };

  // handleNumericClick = e => {
  //   e.stopPropagation();
  // };

  // handlePickWinner = e => {
  //   const { team, round, division, matchId, winner } = this.props;

  //   if (round > 1) {
  //     return false;
  //   } else if (round === 1) {
  //     this.props.unPickWinner({
  //       team,
  //       round,
  //       division,
  //       matchId,
  //       winner: false
  //     });
  //   } else if (round === 0) {
  //     this.props.pickWinner({
  //       team,
  //       round,
  //       division,
  //       matchId,
  //       winner: this.props.team.slug
  //     });
  //   }
  // };

  render() {
    let scoreColor = "is-light";
    let textWeight = "";
    if (this.props.team && this.props.status === "darkGreen") {
      // winning
      scoreColor = "is-success";
      textWeight = "has-text-weight-bold";
    }
    if (this.props.team && this.props.status === "lightGreen") {
      // losing
      scoreColor = "is-success is-light";
    }
    if (this.props.team && this.props.status === "lightRed") {
      // winning
      scoreColor = "is-danger is-light";
      textWeight = "has-text-weight-bold";
    }
    if (this.props.team && this.props.status === "darkRed") {
      // losing
      scoreColor = "is-danger";
    }

    let scoreStyle = "is-medium " + scoreColor;

    const teamStatus = this.props.team
      ? this.props.winner
        ? this.props.winner === this.props.team.name
          ? "has-background-link has-text-white " + textWeight
          : "has-background-warning has-text-danger " + textWeight
        : ""
      : "";


    const teamName = this.props.team ? `${this.props.team.name}` : "Undecided";

    let score = 0;
    if (this.props.team) {

      let finalScore = this.props.team['scores'].find(p => {
        return p.number === this.props.round
      });

      score = this.props.team.currentRoundScore;
      if (finalScore !== undefined) {
        score = finalScore.score;
      }
    }

    // const teamSeed = this.props.team ? (
    //   this.props.round === 0 && !this.props.winner ? (
    //     <SeedWrapper onClick={this.handleNumericClick}>
    //       <NumericInput
    //         min={1}
    //         max={64}
    //         name={this.props.team.slug}
    //         value={this.props.team.seed}
    //         onChange={this.props.handleSeedChange}
    //         style={numericInputStyles}
    //       />
    //     </SeedWrapper>
    //   ) : (
    //     <div>{this.props.team.seed}</div>
    //   )
    // ) : (
    //   ""
    // );

    const teamScore = this.props.team ? (
      <Tag className={scoreStyle}>{Math.round(score * 10000) / 100}</Tag>
    ) : (
      ""
    );

    return (
      <Box className={teamStatus} onClick={this.handlePickWinner}>
        <span className="is-size-4">{teamName} </span>
        {teamScore}
      </Box>
    );
  }
}

export default Team;
