import React, { Component } from "react";
import './App.scss';
import { bracket, rounds, market_holidays } from "./data/bracket-team-data";
import Bracket from "./components/Bracket";
import axios from 'axios';
import GA4React, { useGA4React } from "ga-4-react";

const TRACKING_ID = "G-ECS66VT57V"; // YOUR_OWN_TRACKING_ID
const ga4react = new GA4React(TRACKING_ID);

ga4react.initialize().then((ga4) => {
  ga4.pageview('path')
  ga4.gtag('event', 'pageview', 'path') // or your custom gtag event
}, (err) => {
  console.error(err)
})

const activeTeams = 'CRM|FCX|ALL|GE|HD|UNH|COST|BAC|CVS|IBM|TMUS|ASML|AMD|CVX|AXP|PPG|DD';
const indexes = 'SPY|QQQ|DIA|RSP'
// const secondRoundLosers = 'PG|POOL|LMT|DE|PFE|BSX|DXCM|GS|MSFT|DIS|AAPL|V|ETR|PEP|MCD|';
// const firstRoundLosers = 'CAT|BA|TRV|HON|NKE|MLM|CLX|ADSK|WBA|JPM|AMGN|JNJ|MRK|WMT|HUM|LOW|INTC|CSCO|VZ|QCOM|VIAC|AMAT|ADBE|MS|DOW|MMM|KO|NEE|MA|SBUX|PYPL|HAL';

class App extends Component {
  constructor(props) {
    super(props);


    this.state = {
      bracket,
      rounds: this.caluclateElapsedTradingDays(),
      market_holidays,
      hasTeams: false,
      hasQuotes: false,
      teams: [],
      currentQuotes: [],
    };

    this.coinFlips = [];
  }



  componentDidMount() {
    const teamsUrl = `./data/teams.json`;

    const quoteUrl = 'https://quote.cnbc.com/quote-html-webservice/restQuote/symbolType/symbol?symbols=' + activeTeams + '|' + indexes + '&requestMethod=itv&noform=1&partnerId=2&fund=1&exthrs=1&output=json&events=1'
    // const quoteUrl = './data/intradayQuotes.json'

    axios.all([
      axios.get(teamsUrl),
      axios.get(quoteUrl)
    ])
      .then(axios.spread((obj1, obj2) => {

        let teams = this.calculateCurrentScore(obj1.data.teams, obj2.data.FormattedQuoteResult.FormattedQuote)
        // console.log(obj1.data.teams)
        this.setState({
          teams: teams,
          hasTeams: true,
          currentQuotes: obj2.data.FormattedQuoteResult.FormattedQuote,
          hasQuotes: true,
        })
      }));


  }

  caluclateElapsedTradingDays = () => {
    let retRounds = rounds;
    let currentDateRaw = new Date();
    currentDateRaw.setHours(0, 0, 0, 0);

    let currentDate = currentDateRaw.toISOString().substring(0, 10);
    let currentRoundElapsed = 0;

    for (const i in retRounds) {
      let startDate = retRounds[i].start;

      let endDate = retRounds[i].end;

      let tradingDays = retRounds[i].tradingDaysTotal


      if (startDate > currentDate) {
        retRounds[i].tradingDaysElapsed = 0;
      } else if (endDate < currentDate) {
        retRounds[i].tradingDaysElapsed = tradingDays;
      } else {
        let roundDateRaw = new Date(startDate);
        let roundDate = startDate;
        while (roundDate < currentDate && currentRoundElapsed <= tradingDays) {
          console.log(roundDate);
          if (roundDateRaw.getDay() > 0 && roundDateRaw.getDay() < 6 && !market_holidays.includes(roundDate)) {
            currentRoundElapsed++;
          }
          roundDateRaw.setDate(roundDateRaw.getDate() + 1);
          roundDate = roundDateRaw.toISOString().substring(0, 10);

        }
        retRounds[i].tradingDaysElapsed = currentRoundElapsed;

        console.log(currentRoundElapsed)
      }

    }
    return retRounds;
  }

  calculateCurrentScore = (teams, quotes) => {
    // let teams = this.state.teams;
    // let quotes = this.state.currentQuotes;
    let rounds = this.state.rounds;

    let smStartValue = 0;
    let smCurrentValue = 0;

    for (const i in quotes) {
      let quote = quotes[i];
      let team = teams[quote.symbol];

      let latestPrice = quote['last'];
      let latestTimeStamp = quote['last_time'];

      let currentScore = null;

      // console.log(quote);
      // console.log(quote.symbol + ' ' + team['prices']);


      for (const j in rounds) {
        let startPrice = team['prices'].find(p => {
          return p.date === rounds[j].start
        });

        let endPrice = team['prices'].find(p => {
          return p.date === rounds[j].end
        });



        // console.log(startPrice);
        if (startPrice !== undefined && endPrice === undefined) {
          currentScore = latestPrice / startPrice['open'] - 1
          teams[quote.symbol]['currentRoundScore'] = currentScore
          teams[quote.symbol]['latestPrice'] = latestPrice
          teams[quote.symbol]['currentScoreDate'] = latestTimeStamp

          if (activeTeams.split("|").includes(quote.symbol)) {
            let smStartValueComp = rounds[j].stakes;
            let smEndValueComp = smStartValueComp + smStartValueComp * currentScore;

            smStartValue += smStartValueComp;
            smCurrentValue += smEndValueComp;
          }

          // console.log('Symbol: ' + quote.symbol + ' Current Price: ' + latestPrice + ' Current Score: ' + currentScore);
        }

        if (startPrice === undefined && endPrice === undefined) {
          break;
        }
      }
    }

    console.log(smStartValue);
    console.log(smCurrentValue);

    let smCurrentScore = smCurrentValue / smStartValue - 1
    teams['STONKS']['currentRoundScore'] = smCurrentScore



    return teams;

  }

  /* Ability to manually pick & unpick winner. WIP */

  pickWinner = ({ team, round, division, matchId, winner }) => {
    // return;

    // State is updating, but not getting passed down to round 2 as it should

    // Arbitrary set state of teamA to "win"
    let bracket = { ...this.state.bracket };

    // Set up next match as well
    const nextRound = parseInt(round) + 1;
    const nextMatchId = Math.floor(matchId / 2);
    const teamLocation = matchId % 2 ? "teamB" : "teamA";

    console.log("d", division);
    console.log(round);
    console.log(matchId);
    console.log(winner);

    // Set the pick!
    bracket[division][round][matchId].winner = winner;
    bracket[division][nextRound][nextMatchId][teamLocation] = winner;

    console.log("hey", winner);

    this.setState({
      bracket
    });
  };

  unPickWinner = ({ team, round, division, matchId, winner }) => {
    // return;

    // State is updating, but not getting passed down to round 2 as it should

    // Arbitrary set state of teamA to "win"
    let bracket = { ...this.state.bracket };

    // Set up next match as well
    const nextRound = parseInt(round) + 1;
    const nextMatchId = Math.floor(matchId / 2);
    const teamLocation = matchId % 2 ? "teamB" : "teamA";

    console.log("d", division);
    console.log(round);
    console.log(matchId);
    console.log(winner);

    // Unset this box
    bracket[division][round][matchId].winner = false;

    // Unset the child box
    bracket[division][nextRound][nextMatchId * 2][teamLocation] = false;

    // Unset the parent box
    bracket[division][round - 1][matchId * 2].winner = false;
    // bracket[division][nextRound - 1][nextMatchId * 2][teamLocation] = false;

    this.setState({
      bracket
    });
  };

  render() {
    return (
      <div className="App">
        {this.state.hasTeams && this.state.hasQuotes ? (
          <Bracket
            rounds={this.state.rounds}
            bracket={this.state.bracket}
            teams={this.state.teams}
            handleSeedChange={this.handleSeedChange}
            determineWinner={this.determineWinner}
            pickWinner={this.pickWinner}
            unPickWinner={this.unPickWinner}
          />
        ) : (
          <h3>Loading...</h3>
        )}

      </div>

    );
  }

}

export default App;
